.navbar {
  position: fixed !important;
  width: 100%;
  background-color: #003140 !important;
  top: 0;
  z-index: 999;
}

.navbar a {
  color: white !important;
}

.intro-page {
  background: #003140;
  margin-top: 30vh;
}

.intro-container {
  display: flex;
}

.intro-container h5 {
  margin-top: -5vw;
}

.intro-wireframe {
  transform: rotate3d(0.4, -1, 0, 20deg);
  top: calc(2vw + 90px);
  left: -1vw;
  width: 25vw;
  transition: 2s transform;
}

.intro-wireframe:hover {
  transform: rotateY(0deg);
}

.intro-gif {
  width: 40vw;
}

.intro-mask {
  display: flex;
  align-items: flex-start;
  background-color: white;
  margin-left: -5vw;
  width: 65vw;
  height: 18vw;
}

@keyframes intro-title-anim {
  0% {opacity: 0;}
  5% {opacity: 0;}
  20% {opacity: 1;}
  99% {opacity: 1;}
  100% {opacity: 0;}
}

.intro-mask h1 {
  /* text-transform: uppercase; */
  margin-bottom: 2vh;
  margin-left: 10vw;
  font-size: 3.2vw;
  color: #003140;
  animation: intro-title-anim 20s infinite;
}

@keyframes slide {
  0% { margin-left: 4.5vw; opacity: 1; box-shadow: 0 0 0 #0031402f;}
  5% { margin-left: 50vw; opacity: 1; box-shadow: -30px 15px 10px #0031402f;}  
  10% { margin-left: 50vw; opacity: 0; box-shadow: 0 0 0 #0031402f;}
  100% { margin-left: 50vw; opacity: 0; box-shadow: 0 0 0 #0031402f;}
}

.intro-title-mask {
  position: absolute;
  background-color: #003140;
  width: 5vw;
  height: 5vw;
  margin-left: 4.5vw;
  box-shadow: -30px 0 60px #0031402f;
  border-radius: 10px;
  animation: slide 20s cubic-bezier(0.445, 0.050, 0.550, 0.950) infinite;
}

.intro-code-screen {
  top: calc(30vh + 16vw);
  padding: 0 7vw;
  max-height: 15vw;
  overflow-y: hidden;
}

.intro-code-screen img {
  border-top: 1px solid black;
  box-shadow: 1px -2px 6px black;
  width: 50vw;
}

.intro-icon-react img,
.intro-icon-node img {
  width: 10vw;
}

.intro-icon-mongo img {
  width: 5vw;
}

@keyframes rotate {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(-359deg);}
}

.intro-icon-react {
  top: 90px;
  right: 5vw;
  animation: rotate 30s linear infinite;
}

.intro-icon-react::after {
  content: 'ReactJS';
  margin-left: -8.5vw;
  margin-top: 6vw;
  font-size: 2vw;
}

@keyframes floating {
  30% { transform: translateX(4.5vw) translateY(-8vw) rotate(-10deg);}  
  50% { transform: translateY(-20vh)}  
}

.intro-icon-node { 
  transform: rotate(30deg);
  right: 5vw;
  top: calc(20vh + 15vw);
  animation: floating 20s linear infinite;
}

@keyframes floating2 { 
  50% { transform: translateY(-10vh) rotate(-10deg)}  
}

.intro-icon-mongo { 
  transform: rotate(10deg);
  right: 15vw;
  top: 15vw;
  animation: floating2 14s linear infinite;
}

.intro-icon-mongo::after {
  content: 'MongoDB';
  color: #003140;
  font-size: 1.5vw;
} 

.summary {
  background-color: #f0f0f0;
  padding-top: 6vw;
  padding-bottom: 3vw;
}

.footer {
  background-color: #003140;
  padding-top: 20px;
  padding-bottom: 20px;
  color: white;
}